import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Col, Row, Card, Form, Select, Input, InputNumber, Checkbox } from 'antd';
import { createBill, updateBill, getBillById } from 'services/bill';
import { getQueryParams } from 'utils/urlUtil';

// const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormBillPage' })
class FormBillPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ dataId: queryParamId });
      this.getDataById(queryParamId);
    }
  };

  getDataById = async id => {
    const res = await getBillById({ id });
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    console.log('values', values);
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createBill(values);
      } else {
        await updateBill({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} hóa đơn`}>
        <Row>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col span={24}>
                <Form.Item hasFeedback label="Năm tài chính" {...formItemLayout}>
                  {getFieldDecorator('fiscalYear', {
                    initialValue: initialValue.fiscalYear,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item hasFeedback label="Tên loại hóa đơn" {...formItemLayout}>
                  {getFieldDecorator('label', {
                    initialValue: initialValue.label,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Ký hiệu mẫu hóa đơn" hasFeedback>
                  {getFieldDecorator('invoiceTemplate', {
                    initialValue: initialValue.invoiceTemplate,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Ký hiệu hóa đơn" hasFeedback>
                  {getFieldDecorator('invoiceCode', {
                    initialValue: initialValue.invoiceCode,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Số tồn đầu kỳ từ" hasFeedback>
                  {getFieldDecorator('previousRemainFrom', {
                    initialValue: initialValue.previousRemainFrom,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<InputNumber />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Số tồn đầu ký đến" hasFeedback>
                  {getFieldDecorator('previousRemainTo', {
                    initialValue: initialValue.previousRemainTo,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<InputNumber />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Số phát hành trong kỳ từ" hasFeedback>
                  {getFieldDecorator('publishFrom', {
                    initialValue: initialValue.publishFrom,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<InputNumber />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Số phát hành trong kỳ đến" hasFeedback>
                  {getFieldDecorator('publishTo', {
                    initialValue: initialValue.publishTo,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<InputNumber />)}
                </Form.Item>
              </Col>
            </Row>
            {dataId ? (
              <Fragment>
                <Row>
                  <Col span={24}>
                    <Form.Item {...formItemLayout} label="Số hóa đơn xóa" hasFeedback>
                      {getFieldDecorator('noOfDelete', {
                        initialValue: initialValue.noOfDelete,
                        rules: [
                          {
                            required: true,
                            message: 'Vui lòng nhập thông tin!',
                          },
                        ],
                      })(<InputNumber disabled={dataId !== false} />)}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={24}>
                    <Form.Item {...formItemLayout} label="Danh sách các số hóa đơn xóa" hasFeedback>
                      {getFieldDecorator('deletedReceipt', {
                        initialValue: initialValue.deletedReceipt,
                      })(<Input disabled={dataId !== false} />)}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={24}>
                    <Form.Item {...formItemLayout} label="Số hóa đơn mất" hasFeedback>
                      {getFieldDecorator('noOfLost', {
                        initialValue: initialValue.noOfLost,
                        rules: [
                          {
                            required: true,
                            message: 'Vui lòng nhập thông tin!',
                          },
                        ],
                      })(<InputNumber disabled={dataId !== false} />)}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={24}>
                    <Form.Item {...formItemLayout} label="Danh sách số hóa đơn mất" hasFeedback>
                      {getFieldDecorator('lostReceipt', {
                        initialValue: initialValue.lostReceipt,
                      })(<Input disabled={dataId !== false} />)}
                    </Form.Item>
                  </Col>
                </Row>
              </Fragment>
            ) : (
              undefined
            )}
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Còn hiệu lực">
                  {getFieldDecorator('isActive', {
                    valuePropName: 'checked',
                    initialValue: initialValue.isActive,
                  })(<Checkbox />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item {...formItemLayout} label="Loại hóa đơn">
                  {getFieldDecorator('configTypeId', {
                    initialValue: initialValue.configTypeId || 0,
                  })(
                    <Select>
                      <Select.Option key={0} value={0}>
                        Nhập học
                      </Select.Option>
                      <Select.Option key={1} value={1}>
                        Học phí tín chỉ
                      </Select.Option>
                      <Select.Option key={2} value={2}>
                        Lệ phí
                      </Select.Option>
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormBillPage {...props} />
  </Page>
));



